import ReactToggle from 'react-toggle'
import "react-toggle/style.css"
import './toggle.less'
import UnderlineText from '../UnderlineText/UnderlineText'

export default function Toggle({ label, className, checked, onClick, tip }) {

  const click = (e) => {
    onClick && onClick(!checked)
  }
  return (
    <div className='l-toggle'>
      {
        tip ?
          <UnderlineText tip={tip}>
            <span className='label-text'>{label}</span>
          </UnderlineText>
          : <span className='label-text'>{label}</span>
      }

      <ReactToggle onClick={click}
        checked={checked}
        className={className}
        onChange={click}
        icons={false} />
    </div>
  )
}